*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

header ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
header ul .subnav-content .subnav-content-link {
  color: #000;
  display: block;
  float: none;
  font-size: 0.875rem;
  font-weight: 300;
  padding: 12px;
  text-align: left;
  text-decoration: none;
}
@media (min-width: 1024px) {
  header ul .subnav-content {
    display: none;
  }
}
header .nav-banner-header {
  display: none;
}
@media (min-width: 1024px) {
  header .nav-banner-header {
    background-color: #EDEDED;
    display: block;
    width: 100%;
  }
  header .nav-banner-header .nav-banner-language-container {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    max-width: 1170px;
    padding: 0;
  }
  header .nav-banner-header .nav-banner-language-container .nav-banner-language .nav-banner-btn {
    background-color: #EBEBEB;
    border: none;
    color: #008948;
    cursor: pointer;
    font-size: 0.875rem;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
  }
  header .nav-banner-header .nav-banner-language-container .nav-banner-language .btn-eng {
    color: #000;
    margin-right: 0.3rem;
  }
}
header #navbar {
  align-items: center;
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  padding: 1rem 0;
  padding-left: 1rem;
  z-index: 99;
}
@media (min-width: 1024px) {
  header #navbar {
    display: grid;
    grid-template-columns: auto 1fr 40px;
    margin: 0 auto;
    max-width: 1170px;
    padding: 0;
  }
}
@media (min-width: 1024px) {
  header #navbar .nav-logo-container {
    margin-right: 3rem;
  }
}
header #navbar .nav-menu.active {
  left: 0;
}
header #navbar .nav-menu {
  background-color: #EBEBEB;
  display: grid;
  flex-direction: column;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(7, auto);
  left: -100%;
  min-height: 100%;
  text-align: center;
  top: 4.9rem;
  transition: 0.3s;
  width: 100%;
  z-index: 99;
}
header #navbar .nav-menu .nav-item {
  cursor: pointer;
  list-style-type: none;
  margin: 0 2rem;
  padding: 1rem 0;
  position: relative;
  text-align: left;
  white-space: nowrap;
}
header #navbar .nav-menu .nav-item .nav-link {
  color: #000;
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 58px;
  position: static;
  text-decoration: none;
  transition: 0.3s ease-in-out;
}
header #navbar .nav-menu .nav-item .nav-link::after {
  color: #000;
  content: "+";
  display: block;
  font-size: 1.5rem;
  font-weight: 100;
  position: absolute;
  right: 0.3rem;
  top: 2.5rem;
  transform: translateY(-50%);
  transition: 0.3s ease-in-out;
}
@media (min-width: 1024px) {
  header #navbar .nav-menu .nav-item .nav-link::after {
    display: none;
  }
}
header #navbar .nav-menu .nav-item .nav-link:active,
header #navbar .nav-menu .nav-item .nav-link:focus {
  color: #008948;
}
header #navbar .nav-menu .nav-item .nav-link:focus::after,
header #navbar .nav-menu .nav-item .nav-link:active::after {
  content: "-";
}
@media (min-width: 1024px) {
  header #navbar .nav-menu .nav-item {
    border-radius: 5px;
    cursor: pointer;
    margin: 1rem 0.2rem;
    padding: 0;
    text-align: center;
  }
  header #navbar .nav-menu .nav-item .nav-link {
    color: #000;
    font-size: 1rem;
    font-weight: 400;
    position: static;
    text-decoration: none;
    transition: 0.3s ease-in-out;
  }
}
header #navbar .nav-menu .nav-banner {
  position: relative;
  margin-bottom: 2rem;
}
header #navbar .nav-menu .nav-banner .nav-banner-language {
  padding-top: 0.75rem;
  position: absolute;
  right: 1.75rem;
}
header #navbar .nav-menu .nav-banner .nav-banner-language .nav-banner-btn {
  background-color: #EBEBEB;
  border: none;
  color: #008948;
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 24px;
  text-align: center;
}
header #navbar .nav-menu .nav-banner .nav-banner-language .btn-eng {
  color: #000;
  margin-right: 0.3rem;
}
@media (min-width: 1024px) {
  header #navbar .nav-menu .nav-banner {
    display: none;
  }
}
header #navbar .nav-menu .search-container {
  display: none;
}
header #navbar .nav-menu .search-container .search-container .active {
  display: block;
}
header #navbar .nav-menu .search-container .search-input {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'%3E%3Cpath d='M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z'/%3E%3Cpath d='M0 0h24v24H0z' fill='none'/%3E%3C/svg%3E");
  background-position: 5% center;
  background-repeat: no-repeat;
  background-size: 18px 18px;
  border: 1px solid #DDDDDD;
  border-radius: 0.5rem;
  border-radius: 26px;
  color: #000;
  font-size: 0.875rem;
  font-weight: 400;
  left: 0;
  line-height: 1.5;
  margin: 0 auto;
  outline: none;
  padding: 0.5rem;
  width: 80%;
}
header #navbar .nav-menu .search-container .search-input::placeholder {
  color: #AAA;
  padding: 0 0 0 2rem;
}
@media (min-width: 1024px) {
  header #navbar .nav-menu .search-container {
    display: none;
  }
}
@media (min-width: 1024px) {
  header #navbar .nav-menu {
    all: unset;
    background-color: #fff;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: 1fr;
    height: 100%;
    margin-top: 0;
    position: static;
    text-align: center;
    transition: 0.3s;
    width: 100%;
    z-index: 99;
  }
}
header #navbar .nav-icons {
  align-items: center;
  color: #000;
  cursor: pointer;
  display: flex;
  justify-content: end;
  margin-right: 1.75rem;
  width: 10rem;
}
header #navbar .nav-icons .menu-container .hamburger {
  cursor: pointer;
  display: block;
}
header #navbar .nav-icons .menu-container .hamburger .bar {
  background-color: #000;
  display: block;
  height: 3px;
  margin: 5px auto;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  width: 25px;
}
header #navbar .nav-icons .menu-container .hamburger.active .bar:nth-child(2) {
  opacity: 0;
}
header #navbar .nav-icons .menu-container .hamburger.active .bar:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}
header #navbar .nav-icons .menu-container .hamburger.active .bar:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}
header #navbar .nav-icons .search {
  margin-right: 1.2rem;
}
@media (min-width: 1024px) {
  header #navbar .nav-icons {
    margin-left: 0.75rem;
    width: 100%;
  }
  header #navbar .nav-icons .menu-container {
    display: none;
  }
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}
@media (min-width: 1024px) {
  .sticky {
    position: static;
  }
